.ProductFuncBackground {
    position: fixed;
    background-color: rgb(0, 0, 0, 0.5);
    height: 100%;
    width: 100%;
    top: 0;
    right: 0;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
    transform: translateX(100%);
}

.ProductFunc {
    position: fixed;
    background-color: rgb(247, 247, 247);
    height: 100%;
    top: 0;
    right: 0;
    z-index: 3;
    max-width: 70%;
    transform: translateX(100%);
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
    padding: 100px 45px;
    padding-right: 0;
}

.ProductFunc .product-title {
    color: rgb(102, 109, 146);
    font-size: 14px;
}

.ProductFunc .product-background {
    background-color: #fff;
    padding: 30px;
}

.ProductFunc input[type="file"] {
    display: none;
}

.ProductFunc .input-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    padding: 30px;
    border: 2px dashed rgb(189, 189, 189);
    border-radius: 2px;
    cursor: pointer;
    margin-bottom: 20px;
}

.ProductFunc .input-wrapper svg {
    color: rgb(189, 189, 189);
    margin-bottom: 10px;
}

.ProductFunc .input-wrapper span {
    color: rgb(22, 31, 106);
    font-size: 14px;
}

.ProductFunc .input-wrapper span > span {
    color: rgb(0, 197, 141);
    font-weight: 600;
}

.ProductFunc .img-wrapper {
    width: 100px;
    height: 100px;
    padding: 4px;
    border: 1px solid rgb(234, 234, 234);
}

.ProductFunc .img-wrapper img {
    width: 100%;
    height: 100%;
}

.ProductFunc .product-label {
    color: rgb(22, 31, 106);
    font-weight: 600;
}

.ProductFunc .update-form input,
.ProductFunc .update-form textarea {
    background-color: rgb(238, 238, 238);
    border-radius: 3px;
    padding: 20px;
    border: 2px solid transparent;
    color: rgb(22, 31, 106);
}

.ProductFunc .update-form textarea {
    padding: 10px;
    width: 100%;
}

.ProductFunc .update-form select {
    background-color: rgb(238, 238, 238);
    border-radius: 3px;
    border: 2px solid transparent;
    height: 44px;
    color: rgb(22, 31, 106);
}

.ProductFunc .update-form input:focus,
.ProductFunc .update-form select:focus,
.ProductFunc .update-form textarea:focus {
    border-color: rgb(0, 197, 141);
    background-color: rgb(247, 247, 247);
    outline: none;
    box-shadow: none;
}

.ProductFunc .fa-times {
    color: #ccc;
    cursor: pointer;
    transition: color 0.2s;
}

.ProductFunc .fa-times:hover {
    color: rgb(234, 77, 74);
}

.ProductFunc .update-btn {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 30px 25px;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 3px;
    margin: 0;
    background-color: #fff;
}

.ProductFunc .update-btn .cancle-btn {
    margin-right: 15px;
}

.ProductFunc .update-btn .cancle-btn button {
    background-color: #fff;
    color: rgb(234, 77, 74);
    font-weight: 600;
    border: none;
}

.ProductFunc .update-btn .cancle-btn button:active {
    background-color: rgb(238, 238, 238);
    color: rgb(234, 77, 74);
}

.ProductFunc .update-btn .cancle-btn button:hover {
    background-color: rgb(238, 238, 238);
    color: rgb(234, 77, 74);
}

.ProductFunc .update-btn .submit-btn button {
    background-color: rgb(0, 197, 141);
    border: none;
    font-weight: 600;
}

.ProductFunc .update-btn .submit-btn button:hover,
.ProductFunc .update-btn .submit-btn button:active {
    background-color: rgb(0, 189, 135);
}

.ProductFunc .update-btn button {
    padding: 16px;
    font-size: 14px;
}

.update-active {
    transform: translateX(0);
    opacity: 1;
    pointer-events: all;
}
