.Statistic {
    display: flex;
    flex-direction: column;
    padding: 30px;
    margin-bottom: 20px;
}

.Statistic .header {
    display: flex;
    justify-content: space-between;
}

.Statistic .bt-header {
    margin-bottom: 8px;
}

.Statistic .title div:last-child {
    color: rgb(102, 109, 146);
    font-size: 13px;
    font-weight: 600;
}

.Statistic .icon {
    width: 48px;
    height: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(255, 179, 0);
    border-radius: 15px;
    flex-shrink: 0;
}

.Statistic .number {
    margin: 50px 0;
}

.Statistic .up-down {
    display: flex;
    align-items: center;
}

.Statistic .up svg,
.Statistic .down svg {
    margin-right: 5px;
}

.Statistic .up-down span {
    color: rgb(102, 109, 146);
    height: 100%;
    margin-left: 5px;
    font-weight: 400;
}
