.NotFound {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.NotFound .wrapper {
    max-height: 600px;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.NotFound h1 {
    font-size: 25px;
    font-weight: 700;
    color: rgb(13, 17, 54);
    margin-bottom: 30px;
}

.NotFound .btn {
    margin-top: 50px;
    background-color: rgb(0, 158, 127);
    color: #fff;
    font-weight: 600;
    padding: 10px 40px;
    display: flex;
    align-items: center;
}

.NotFound .btn svg {
    margin-right: 10px;
}

.NotFound .btn:focus {
    box-shadow: none;
}
