.User .nav-links {
    list-style: none;
    position: absolute;
    right: 0;
    top: 11vh;
    height: 91vh;
    width: 25%;
    background-color: #fff;
    box-shadow: 0px 8px 6px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
    z-index: 2;
}

.User .nav-links li {
    transition: opacity 0.5s ease-in;
}

.User .nav-links a {
    text-decoration: none;
    color: rgb(13, 17, 54);
    font-weight: 600;
    letter-spacing: 2px;
    transition: color 0.3s;
}

.User .nav-links a:hover {
    color: rgb(0, 158, 127);
}

.User .welcome {
    color: rgb(13, 17, 54);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 10px 0;
    font-weight: 600;
    letter-spacing: 2px;
    cursor: pointer;
    transition: color 0.3s;
}

.User .welcome:hover {
    color: rgb(0, 158, 127);
}

.User .welcome:hover .Burger .line {
    background-color: rgb(0, 158, 127);
}

.nav-active {
    transform: translateX(0%) !important;
}

.item-active:first-child {
    animation: navLinkFade 0.8s ease-in;
}

.item-active:nth-child(2) {
    animation: navLinkFade 1s ease-in;
}

.item-active:nth-child(3) {
    animation: navLinkFade 1.2s ease-in;
}

.item-active:last-child {
    animation: navLinkFade 1.4s ease-in;
}

@keyframes navLinkFade {
    from {
        opacity: 0;
        transform: translateX(50px);
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}
