.QuantityAdjustment {
    background-color: rgb(0, 158, 127);
    display: flex;
    width: 100px;
    justify-content: space-around;
    align-items: center;
    color: #fff;
    font-weight: 600;
    border-radius: 30px;
    font-size: 16px;
    padding: 5px 0;
    -webkit-user-select: none; /* Chrome/Safari */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* IE10+ */

    /* Rules below not implemented in browsers yet */
    -o-user-select: none;
    user-select: none;
}

.QuantityAdjustment .adj {
    cursor: pointer;
}

.QuantityAdjustment.cart-style {
    flex-direction: column-reverse;
    width: 30px;
    padding: 10px 0;
    background-color: rgb(247, 247, 247);
    color: rgb(13, 17, 54);
}
